import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PersonalInfoComponent } from 'src/app/components/profile/personal-info/personal-info.component';
import { InfoPersonalComponent } from './components/info-personal/info-personal.component';
import { ProfileeditComponent } from './components/profileedit/profileedit.component';
import { TrackMyorderComponent } from './components/track-myorder/track-myorder.component';



const routes: Routes = [
  {path: '',  component: InfoPersonalComponent},

  {path: 'edit',  component: ProfileeditComponent},
  { path: 'track-order',  component: TrackMyorderComponent },
//   {path: ,  component: OnlyProductViewComponent}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
exports: [RouterModule]
})
export class ProfileRoutingModule { }


