import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/core/services/auth/auth.service';
import { UserData } from 'src/app/modules/auth/models/userData.model';
import { FixRequestService } from 'src/app/modules/fix-request/services/fix-request.service';
import { RepairRequest } from '../../model/repaireRequest.model';

@Component({
  selector: 'app-my-requests',
  templateUrl: './my-requests.component.html',
  styleUrls: ['./my-requests.component.scss']
})
export class MyRequestsComponent implements OnInit {
  userData: UserData;
  requestData: RepairRequest[];
  constructor(
    private _fixServ: FixRequestService, private authSrvc: AuthService,) { }

  ngOnInit(): void {
    this.authSrvc.userData$.subscribe((data) => {
      if (data) this.userData = data;
    });
    this.getMyRequests()
  }


  getMyRequests() {
    this._fixServ.getAllUserRequests(this.userData.id)
      .subscribe((res) => {
        this.requestData = res.data
      })
  }
}
